// header
import commonHeader from"@/components/common-header/common-header";
// tabbar 
import tabbar from"@/components/common-tabbar/common-tabbar";
import testCode from '@/components/testCode/testCode';
export default {
	data() {
		return {
			testCode:2,
			phoneMaskShow:false,
			phone:"",
			hideBox: true,
			hideTips:false,
			btnState: true,
			selectCode: false,
			disabled: false,
			code:"",
			codeState:false,
			codeText: "获取验证码",
		};
	},
	components:{
		commonHeader,
		tabbar,
		testCode
	},
	methods:{
		// 获取输入手机号
		enter(e){
			if(e.detail.value.length===0){
				this.testCode = 2;
				this.selectCode = false;
			}else if(e.detail.value.length===11){
				this.testCode = 0;
				this.selectCode = true;
				this.phone = e.detail.value;
				// this.phoneMaskShow = true;
			}else{
				this.testCode = 1;
				this.selectCode = false;
			}
			this.submitState();
		},
		// 获取验证码
		gettestcode(e){
			if(e.detail.value.length===6){
				this.codeState = true;
				this.code = e.detail.value;
			}else{
				this.codeState = false;
			}
			this.submitState();
		},
		// 判断输入按钮状态
		submitState(){
			if(this.testCode===0&&this.codeState){
				this.btnState = false;
			}else{
				this.btnState = true;
			}
		},
		// 更换手机号
		submit(){
			this.phoneMaskShow = false;
			// 返回上一页


















			uni.navigateBack(1)
		},
		// 获取输入密码
		getPwd(val) {
			console.log(val)
			if(val==888888){
				this.hideBox = true;
				this.phoneMaskShow=true;
				uni.reLaunch({
					url:"../login/login"
				})
			}else{
				this.hideTips = true;
			}
		},
		// 获取验证码
		getCode() {
			if (this.selectCode) {
				this.disabled = true;
				this.codeHide = false;
				var time = 59;
				this.codeText = time + 's后重新获取';
				var setTime = setInterval(() => {
					time--;
					this.codeText = time + 's后重新获取';
				}, 1000)
				setTimeout(() => {
					this.codeText = "获取验证码";
					this.disabled = false;
					this.codeHide = true;
					clearInterval(setTime);
				}, 59000)
			}
		},
	}
}
